import {mapState, mapMutations} from 'vuex'
export default {
	data() {
		return {
			pass:'',
			passAgn:'',
			codeBut:'发送验证码',
			codeClick:true,
			code:''
		}
	},
	computed:{
		...mapState({
			mobilephone:state=>state.user.mobilephone
		}),
		getVaguePhoneNumber(){
			var array = this.mobilephone
			var mphone = array.substring(0, 3) + '****' + array.substring(7)

			return mphone
		}
	},
	methods: {
		passInput(e){
			this.pass = e.detail.value
		},
		passAgnInput(e){
			this.passAgn = e.detail.value
		},
		phoneCodeInput(e){
			this.code = e.detail.value
		},
		setPassword(){
			var _self = this
			if(_self.pass == ''){
				uni.showToast({title: '请输入密码', icon:'none', duration: 1500})
				return
			}
			if(_self.pass != _self.passAgn){
				uni.showToast({title: '两次输入密码不一样', icon:'none', duration: 1500})
				return
			}
			if(_self.code == ''){
				uni.showToast({title: '请输入验证码', icon:'none', duration: 1500})
				return
			}
			uni.showLoading({title:'', mask:true})
			_self.$H.post('/api/Finance/SetTakePassword',{
				TakePassword:_self.pass,
				TakePasswordAgn:_self.passAgn,
				Code:_self.code
			},{
				token:true
			}).then(res=>{	
				console.log(res)
				uni.hideLoading()
				if(res.status == 0){
					uni.showToast({title: '设置提现密码成功', icon:'none', duration: 1500})
					setTimeout(()=>{
						uni.navigateBack({
							delta:1
						})
					},1500)
				}else{
					uni.showToast({title:res.message, icon:'none', duration:1000})
				}
			})
		},
		sendphoneCode(){
			var _self = this
			if(_self.codeClick){
				//发送验证码
				_self.sendCode()
				_self.codeClick = false
				// 倒计时开始
				var daojishi = 60
				_self.codeBut = "重新发送" + daojishi;
				_self.timer = setInterval(function(){
					daojishi--
					_self.codeBut = "重新发送" + daojishi;
					if(daojishi <= 0){
						_self.codeBut = "重新发送"
						_self.codeClick = true
						clearInterval(_self.timer);
					}
				}, 1000)
			}
		},
		sendCode(){
			var _self = this
			_self.$H.post('/api/TakeAccount/SendCode',{},{
				token:true
			}).then(res=>{
				if(res.status === 0){
					
				}else{
					uni.showToast({title:res.message, icon:'none', duration:1000})
				}
			})
		}
	}
}
